 ////= ../../bower_components/jquery/dist/jquery.js


/*
 * Custom
 */
$(function() {

	$(".js-goreg-form").click(function(e) {
		setTimeout(function() {
			$(".js-reg-form").click();
		}, 100);
	});

	// Фокус в первом поле 
	$('.uk-modal').on({
		'show.uk.modal': function(){
			$(this).find("input").first().focus();
		}
	});

	// Стилизованные чекбоксы
	$(".custom-checkbox :checkbox").change(function() {
		if ($(this).prop("checked") == true) {
			$(this).closest(".custom-checkbox").addClass("custom-checkbox_checked")
			.removeClass("custom-checkbox_unchecked");
		} else {
			$(this).closest(".custom-checkbox").addClass("custom-checkbox_unchecked")
			.removeClass("custom-checkbox_checked");
		}
	});

});